import { useEffect, useState } from "react";
import "./styles.css";

export default function App() {
  const [time, setTime] = useState(0);
  const [startClicked, setStartClicked] = useState(false);
  const [intervalVal, setIntervalVal] = useState(0);
  useEffect(() => {
    if (startClicked) {
      const intervalId = setInterval(() => {
        setTime((time) => time + 1);
      }, 1000);
      setIntervalVal(intervalId);
    }
  }, [startClicked]);

  const handleReset = () => {
    clearInterval(intervalVal);
    setTime(0);
    setStartClicked(false);
  };

  const getTime = (time) => {
    return (
      <>
        <span>{Math.floor(time / 60)}</span>:
        <span>{Math.floor(time % 60)}</span>
      </>
    );
  };

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>
      <h2>{getTime(time)}</h2>
      <button
        style={{
          background: "blue",
          width: "100px",
          height: "50px",
          color: "white"
        }}
        onClick={() => setStartClicked(true)}
      >
        Start
      </button>
      &nbsp;&nbsp;
      <button
        style={{
          background: "blue",
          width: "100px",
          height: "50px",
          color: "white"
        }}
        onClick={handleReset}
      >
        Reset
      </button>
    </div>
  );
}
